import { Wynik, Okreg, Sondaze, ILE_SYMULACJI } from "./typy";
import { okregi, sondaze, KOLOR_PARTII } from "./dane";
import {
  NaDemo,
  PodzialMandatowKraj,
  PodzialMandatowWOkregu,
  PoparcieWOkregu,
  RozkladPrawdopodobienstwaWOkregu,
} from "./ordynacja";
import {
  rysujHistogram,
  rysujWynikWyborow,
} from "./wykresy";
import { komentarze } from "./komentarz";

const wynikiWgSondazy: Sondaze = {};
Object.entries(sondaze).forEach(
  ([sondaz, wynik]) =>
    (wynikiWgSondazy[sondaz] = PodzialMandatowKraj(wynik as Wynik))
);

const sredniaSondazy: Wynik = {};
Object.entries(sondaze).forEach(([sondaz, wynik]) => {
  Object.entries(wynik).forEach(([partia, procent]) =>
    sredniaSondazy[partia]
      ? (sredniaSondazy[partia] += procent)
      : (sredniaSondazy[partia] = procent)
  );
});
Object.entries(sredniaSondazy).forEach(([partia, procent]) => {
  sredniaSondazy[partia] =
    sredniaSondazy[partia] / Object.entries(sondaze).length;
});

const podzialWOkregach: Sondaze = {};
const podzialWOkregachDemo: Sondaze = {};
okregi.forEach((okreg) => {
  const wynikWOkregu = PoparcieWOkregu(sredniaSondazy, okreg);
  podzialWOkregach[okreg.miasto] = PodzialMandatowWOkregu(wynikWOkregu, okreg);
  podzialWOkregachDemo[okreg.miasto] = NaDemo(
    PodzialMandatowWOkregu(wynikWOkregu, okreg)
  );
});

export function RysujRozkladyWOkregu(wynik: Wynik, okreg: Okreg) {
    // console.log(wynik, okreg);
  const { rozklad, histogram, wynikWOkregu } = RozkladPrawdopodobienstwaWOkregu(
    sredniaSondazy as Wynik,
    okreg,
    {
      laczOpozycje: true,
    }
  );
  const wykresRozkladu: Sondaze = {};
  rozklad.slice(0, 3).forEach(({ key, value }) => {
    wykresRozkladu[(value / (ILE_SYMULACJI / 100)).toString() + "%"] =
      JSON.parse(key) as Wynik;
  });

  const { rozklad: rozkladDemo } = RozkladPrawdopodobienstwaWOkregu(
    sredniaSondazy as Wynik,
    okreg,
    { laczOpozycje: false }
  );
  const wykresRozkladuDemo: Sondaze = {};
  rozkladDemo.slice(0, 5).forEach(({ key, value }) => {
    wykresRozkladuDemo[(value / (ILE_SYMULACJI / 100)).toString() + "%"] =
      JSON.parse(key) as Wynik;
  });

  const text = document.createElement("h2");
  text.innerHTML = okreg.miasto;
  text.setAttribute("id", okreg.miasto + 'Drawer');

  document.getElementsByTagName("body")[0].appendChild(text);
  const histogramKontener = document.createElement("div");
  histogramKontener.setAttribute("style", "width: 20%; display: flex;");
  document.getElementsByTagName("body")[0].appendChild(histogramKontener);
  Object.entries(sredniaSondazy).forEach(([partia, procent]) => {
    const elementName = okreg.miasto + "Histogram" + partia;
    const wykresHistogram = document.createElement("canvas");
    wykresHistogram.setAttribute("id", elementName);
    histogramKontener.appendChild(wykresHistogram);
    rysujHistogram(
      histogram[partia],
      elementName,
      KOLOR_PARTII[partia],
      partia,
      (Math.round(wynikWOkregu[partia] * 5) / 5).toString()
    );
  });

  if (komentarze[okreg.miasto]) {
    const komentarz = document.createElement("p");
    komentarz.innerHTML = komentarze[okreg.miasto];
    document.getElementsByTagName("body")[0].appendChild(komentarz);
  }

  // const jedenHistogramKontener = document.createElement("div");
  // const jedenWykresHistogram = document.createElement("canvas");
  // jedenWykresHistogram.setAttribute("id", okreg.miasto + "HistogramJeden");
  // jedenWykresHistogram.setAttribute("style", "width: 100%;");
  // jedenHistogramKontener.appendChild(jedenWykresHistogram);
  // document.getElementsByTagName("body")[0].appendChild(jedenHistogramKontener);
  // rysujJedenHistogram(histogram, okreg.miasto + "HistogramJeden");
  // Object.entries(histogram).forEach(([partia, poparcie]) => {
  //     partia <- TODO - transform data to have it in Sondaze format
  // });

  const kontener = document.createElement("div");
  const wykres = document.createElement("canvas");
  const wykresDemo = document.createElement("canvas");
  const naglowek = document.createElement("h3");

  kontener.setAttribute(
    "style",
    "width: 35%; margin-left: 10%; margin-bottom: 100px; display: flex;"
  );

  naglowek.innerHTML = 'Prawdopodobiestwo podziau mandatw';
  naglowek.setAttribute("style", "margin-left: 40%;");
  document.getElementsByTagName("body")[0].appendChild(naglowek);

  wykres.setAttribute("id", okreg.miasto);
  wykresDemo.setAttribute("id", okreg.miasto + "Demo");

  kontener.appendChild(wykres);
  kontener.appendChild(wykresDemo);
  document.getElementsByTagName("body")[0].appendChild(kontener);

  rysujWynikWyborow(wykresRozkladu, okreg.miasto, {
    poziomo: true,
    laczOpozycje: true,
  });
  rysujWynikWyborow(wykresRozkladuDemo, okreg.miasto + "Demo", {
    poziomo: true,
    laczOpozycje: false,
  });
}

(async function () {
  let nrOkregu = 1;
  okregi.forEach((okreg) => {
    const text = document.createElement("a");
    text.innerHTML = okreg.miasto + ` (${nrOkregu})`;
    text.className = "grid-item";
    document.getElementById("miasta").appendChild(text);
    nrOkregu++;
  });
  rysujWynikWyborow(sondaze, "sondaze", { laczOpozycje: false });
  rysujWynikWyborow(wynikiWgSondazy, "mandatyKraj", {
    poziomo: true,
    laczOpozycje: false,
    calySejm: true,
  });
})();
setTimeout(function () {
  rysujWynikWyborow(podzialWOkregach, "mandatyWOkregach", {
    poziomo: true,
    laczOpozycje: false,
  });
  rysujWynikWyborow(podzialWOkregachDemo, "mandatyWOkregachDemo", {
    poziomo: true,
    laczOpozycje: true,
  });
}, 200);
for (const okreg of okregi) {
  let i = 0;
  setTimeout(function () {
    RysujRozkladyWOkregu(sredniaSondazy, okreg);
  }, ++i * 500);
}
